import {App} from 'vue'

import 'element-plus/dist/index.css'
import { ElButton,ElForm,ElFormItem,ElInput,ElRadio,ElAlert,ElAside } from 'element-plus/lib/components'


const components=[ElButton,ElForm,ElFormItem,ElInput,ElRadio,ElAlert,ElAside]

export default function (app:App) :void{
  for(const component of components){
    app.component(component.name,component) //全局注册组件
  }
}